<template>
  <div class="Activitydetails" v-if="activitydata[0]">
    <p class="regular" v-if="activityType==1">
      <strong>特价房活动规则：</strong>{{activitydata[0].activityRules}}
    </p>
    <p class="regular" v-if="activityType==2">
      <strong>钟点房活动规则：</strong>{{activitydata[0].activityRules}}
    </p>
    <p class="regular" v-if="activityType==3">
      <strong>秒杀房活动规则：</strong>{{activitydata[0].activityRules}}
    </p>
    <p class="regular" v-if="activityType==4">
      <strong>团购房活动规则：</strong>{{activitydata[0].activityRules}}
    </p>
    <div class="weui-cells room-list">
      <div class="weui-cell" v-for="(item,index) in activitydata" :key="index" v-if='!(activityType==4 && item.minPurchaseNumber>item.remainder)'>
        <div class="weui-cell__hd list_left">
          <img :src="item.path" />
        </div>
        <div class="weui-cell__bd list_right">
          <div class="title">
            <p>{{item.categoryName}}</p>
            <a class="weui-btn_mini weui-btn weui-btn_plain-primary" v-if="item.remainder<=item.numberThreshold&&item.showRoomEnable==0">剩余{{item.remainder}}间</a>
          </div>
          <div class="details">
            <p class="Price">
              <span class="newPrice">
                <i>￥</i>{{item.activityPrice |money}}</span>
              <a href="javascript:;" class="weui-btn_mini weui-btn weui-btn_plain-primary fr" v-if="activityType!=3" @click="bookingfn(item)">预订</a>
              <a href="javascript:;" class="weui-btn_mini weui-btn weui-btn_plain-primary fr" v-if="activityType==3" @click="bookingfn(item)">秒杀</a>
            </p>
            <times :endTime="item.endTime"></times>
          </div>

        </div>

      </div>
    </div>
  </div>
</template>

<script type='text/ecmascript-6'>
import { homePage } from "@/assets/js/request.js";
import times from "@/components/times";
export default {
  data() {
    return {
      activityType: this.$route.params.activityType,
      hotelId: sessionStorage.getItem("hotelId"),
      activityId: this.$route.params.activityId,
      activitydata: []
    };
  },
  components: {
    times
  },
  methods: {
    bookingfn(item) {
      if (this.activityType == 4 && item.minPurchaseNumber > item.remainder) {
        this.Toast({
          message: "暂无房型",
          duration: 2000
        });
        return false;
      }
      sessionStorage.setItem("storeRoom", JSON.stringify(item));
      this.$router.push("/bookImmediately/" + item.categoryId);
    }
  },
  mounted() {
    homePage.specificActivity({
      Suffix: this.hotelId + "/activity/" + this.activityId,
      fn: data => {
        var _this = this;
        if (data.records.length != 0) {
          this.activitydata = data.records;
        } else {
          this.Toast({
            message: "暂无房型",
            duration: 2000
          });
        }
      }
    });
  }
};
</script>

<style scoped lang='less' rel='stylesheet/less'>

</style>



